import { mapMutations } from "vuex";
import { mapGetters } from "vuex";
import $ from "jquery";
export default {
  data() {
    return {
      itemSelected: 0,
      available: true,
    };
  },
  props: {
    infoProduct: {},
  },
  methods: {
    ...mapMutations({
      addToCart: "localStorage/add",
      removeFromCart: "localStorage/remove",
    }),
    addItem() {
      this.addToCart({
        ...this.infoProduct,
        conditionnement: this.itemSelected,
      });
      this.openPanel()
      this.$emit("moveCard", this.infoProduct.id);
    },
    openPanel() {
      //   this.authPanel = true;
      let id="sidebar-cart"
      $("body").addClass("stopScroll");
      $("#"+id).css("animation", "slideIn 1s forwards");
      $("#"+id).css("display", "block");
      $("#"+id+ ".sidebar-content").css("right", "0");
    },
  },
  computed: {
    ...mapGetters({
      getOptionLivraison: "optionLivraison/optionLivraison",
    }),
    calculPrix() {
      var price = NaN;
          price = parseFloat(
            this.infoProduct.price
              .toString()
              .replace(/,/, ".")
          ).toFixed(2);
          return price;

    },
  },
};
